.player{
    height: 20vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
}

.time-control{
    width: 50%;
    display: flex;
    align-items: center;
    height: 20vh;
    input{
        width: 100%;
        -webkit-appearance: none;
        background: transparent;
        cursor: pointer;
    }
    p{
        padding: 0rem 1rem;
    }
}

.play-control{
    width: 30%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    svg{
        cursor: pointer;
    }
}

input[type="range"]:focus{
    outline: none;
}
input[type="range"]::-webkit-slider-thumb{
    -webkit-appearance: none;
    height: 16px;
    width: 16px;
}
input[type="range"]::-moz-range-thumb{
    -webkit-appearance: none;
   background: transparent;
   border: none;
}

.track{
    background: lightblue;
    width: 100%;
    height: 1rem;
    position: relative;
    border-radius: 1rem;
    overflow: hidden;
}

.animate-track{
    background: rgb(204, 204, 204);
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
    transform: translateX(50%);
    pointer-events: none;
}

@media screen and (max-width: 768px) {
    .time-control{
        width: 90%;
    }

    .play-control{
        width: 70%;
    }
}